@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400,500,700");
.sketchpad {
  width: 100%;
  min-height: 100%;
  position: relative;
  cursor: pointer;
  color: black; }
  .sketchpad .sketchpad-title,
  .sketchpad .sketchpad-confirm {
	text-transform: lowercase;
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100%;
	text-align: center;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	display: block; }
	.sketchpad .sketchpad-title.deactive,
	.sketchpad .sketchpad-confirm.deactive {
	  display: none; }

.sketchpad-canvas {
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .sketchpad-canvas.deactive {
	background-image: none; }

.sketchpad-toolbelt {
  position: absolute;
  bottom: 0;
  display: none; }
  .sketchpad-toolbelt.active {
	display: block; }
  .sketchpad-toolbelt button, .sketchpad-toolbelt a {
