	letter-spacing: 1px;
	color: black;
	background-color: transparent;
	padding: 0.5625rem 0.75rem;
	cursor: pointer; }
	.sketchpad-toolbelt button:first-child, .sketchpad-toolbelt a:first-child {
	  margin-right: 0.9375rem;
