	  .sketchpad-toolbelt button:first-child:hover, .sketchpad-toolbelt a:first-child:hover {
		color: white;
		background-color: black; }
	.sketchpad-toolbelt button:last-child, .sketchpad-toolbelt a:last-child {
	  opacity: .5;
	  border: 0; }
	  .sketchpad-toolbelt button:last-child:hover, .sketchpad-toolbelt a:last-child:hover {
		opacity: 1; }
	  .sketchpad-toolbelt button:last-child::before, .sketchpad-toolbelt a:last-child::before {
		content: '[ '; }
	  .sketchpad-toolbelt button:last-child::after, .sketchpad-toolbelt a:last-child::after {
		content: ' ]'; }



